.glass{
  background: rgba(255, 255, 255, 0.55);
  border-radius: 16px;
  box-shadow: 0 4px 30px #4747470b;
  backdrop-filter: blur(7.1px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  @apply border-4 border-gray-50 shrink-0 h-3/4 w-[30%] rounded-3xl py-20 px-7 min-w-max;
}
.hello{
  @apply border-4 border-gray-200 w-[135px] h-[135px] rounded-full shadow-lg cursor-pointer overflow-hidden;
  @apply hover:border-accent;
}
.profile_img{
  width: 100%;
  height: 100%;
  /* @apply border-4 border-gray-200 w-[150px] h-[150px] rounded-full shadow-lg cursor-pointer overflow-hidden;
  @apply hover:border-accent; */

}

.textbox{
  @apply border-0 px-5 py-4 rounded-xl w-3/4 shadow-sm text-lg;
  @apply focus:outline-none;
}

.btn{
  @apply border bg-indigo-500 w-3/4 py-4 rounded-lg text-gray-50 text-xl shadow-sm text-center; 
}

.btn:hover{
    background: #ff6a6a;
}
input[type ="file"]{
  display: none;
}